.main {
  position: relative;
  float: left;
  width: 100%;
  padding-top: 30px;
  padding-bottom: 30px;

  .container {

    .row {
      margin: 0;
    }
  }

  &--about {
    padding-bottom: 0;

    @include breakpoint(max-width 900px) {
      height: calc(100vh - 167px);
      height: -webkit-calc(100vh - 167px);
      height: -moz-calc(100vh - 167px);
    }

    @include breakpoint(max-width 700px) {
      height: 100%;
    }
  }
  
  &__title {
    position: relative;
    float: left;
    width: 100%;
    margin: 0;
    text-align: center;
    text-transform: uppercase;
    color: #fff;
    font-family: $A;

    @include breakpoint(max-width 600px) {
      padding: 0 15px;
      @include border-box;
      margin-top: 30px;
    }

    &:before {
      display: block;
      position: absolute;
      content: '';
      width: 40px;
      height: 40px;
      top: 11px;
      left: 0;
      background-image: url('../img/icons/burger-icon.png');
      background-repeat: no-repeat;
      -webkit-background-size: contain;
      background-size: contain;
    }

    &:after {
      display: block;
      position: absolute;
      content: '';
      width: 40px;
      height: 40px;
      top: 11px;
      right: 0;
      background-image: url('../img/icons/burger-icon.png');
      background-repeat: no-repeat;
      -webkit-background-size: contain;
      background-size: contain;


    }
  }

  &__subtitle {
    display: block;
    position: relative;
    float: left;
    width: 100%;
    height: 40px;
    margin-top: -16px;
    margin-bottom: 30px;
    background-image: url('../img/subtitle.png');
    background-repeat: no-repeat;
    -webkit-background-size: contain;
    background-size: contain;
    background-position: center center;
  }

  &__content {
    display: block;
    position: relative;
    float: left;
    width: 100%;

    @include breakpoint(max-width 660px) {
      padding: 16px 20px;
      @include border-box;
    }

    &:before {
      display: block;
      position: absolute;
      content: '';
      right: -34px;
      top: -18px;
      width: 50px;
      height: 100px;
      background-image: url('../img/q-top.png');
      background-repeat: no-repeat;
      -webkit-background-size: contain;
      background-size: contain;

      @include breakpoint(max-width 600px) {
        top: 0;
        right: 0;
      }
    }

    &:after {
      display: block;
      position: absolute;
      content: '';
      left: -34px;
      bottom: -18px;
      width: 50px;
      height: 100px;
      background-image: url('../img/q-bottom.png');
      background-repeat: no-repeat;
      -webkit-background-size: contain;
      background-size: contain;

      @include breakpoint(max-width 600px) {
        left: 0;
        bottom: 0;
      }
    }

    p {
      position: relative;
      float: left;
      width: 100%;
      margin-top: 0;
      color: #fff;
      font-size: 16px;
      font-family: $AR;
      line-height: 1.5;
    }

    &--about {
      margin-top: 50px;
      margin-bottom: 100px;

      @include breakpoint(max-width 700px) {
        margin-top: 20px;
        margin-bottom: 20px;
      }
    }
  }

  &__partners {
    @include display-flex(flex);
    @include flex-wrap(wrap);
    @include flex-direction(row);
    @include justify-content(space-between);
    position: relative;
    float: left;
    width: 100%;
    margin-top: 30px;
    margin-bottom: 50px;

    &--item {
      position: relative;
      float: left;
      width: calc(50% - 20px);
      width: -webkit-calc(50% - 20px);
      width: -moz-calc(50% - 20px);
      height: 70px;
      overflow: hidden;
      margin: 0 10px;

      > img {
        width: 100%;
        height: 100%;
        object-fit: contain;
      }
    }
  }

  &__video {
    position: relative;
    float: left;
    width: 100%;
    height: calc(100% - 70px);
    padding-left: 50px;
    @include border-box;

    @include breakpoint(max-width 660px) {
      padding: 0;
      height: 170px;
    }

    video {
      display: block;
      position: relative;
      float: left;
      width: 100%;
      height: 100%;
    }
  }

  &__team {
    position: absolute;
    top: -46px;
    right: 0;
    width: 50%;
    height: calc(100% + 44px);
    height: -webkit-calc(100% + 44px);
    height: -moz-calc(100% + 44px);
    padding-left: 50px;
    @include border-box;

    @include breakpoint(max-width 1024px) {
      top: 0;
      height: 100%;
    }

    @include breakpoint(max-width 700px ) {
      position: relative;
      float: left;
      width: 100%;
      top: inherit;
      right: inherit;
      padding: 0;
    }

    img {
      width: 100%;
      height: 100%;
      object-fit: cover;

      @include breakpoint(max-width 1024px) {
        object-fit: contain;
      }
    }
  }
}

